<template>
    <v-container class="animate-fade-in">
        <v-layout row class="mt-5 pt-5">
            <v-flex xs6 offset-xs3>
                <h1 class="mb-1" :class="{'white--text': lightText}">Choose another theme</h1>
                <v-select
                :items="['red', 'blue', 'green', 'yellow', 'white', 'grey']"
                v-model="selectedTheme"
                placeholder="Select New Theme"
                class="mt-0 pt-0"
                :disabled="changing"
                ></v-select>
                <v-btn
                style="position: relative; right: 7px"
                class="white--text text-capitalize mt-0"
                color="error"
                :disabled="changing"
                @click="changeTheme()"
                >
                Apply
                </v-btn>
            </v-flex>
        </v-layout>
        <v-layout row class="mt-5 pt-5">
            <v-flex xs6 offset-xs3>
                <h1 class="mb-1" :class="{'white--text': lightText}">Change password</h1>
                <v-text-field
                :disabled="changing"
                class="passContainer"
                label="New Password"
                v-model="newPassword"
                required
                :type="'password'"
                ></v-text-field>
                <v-text-field
                :disabled="changing"
                class="passContainer"
                label="Repeat New Password"
                v-model="repeatNewPassword"
                required
                :type="'password'"
                ></v-text-field>
                <p class="animate-fade-in" :class="{'white--text': lightText}" v-if="passMsg"> {{passMsg}} </p>
                <v-btn
                style="position: relative; right: 7px"
                class="white--text text-capitalize mt-0"
                color="error"
                :disabled="changing"
                @click="changePass()"
                >
                Change a password
                </v-btn>
            </v-flex>
        </v-layout>
        <v-layout row class="mt-5 pt-5">
            <v-flex xs6 offset-xs3>
                <v-btn
                style="position: relative; right: 7px"
                class="white--text text-capitalize mt-5"
                color="error"
                :disabled="changing"
                @click="deleteAccount()"
                >
                Pernamently delete an account
                </v-btn>
            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
import firebase from "firebase"
import db from './../firebase/init'
import './../../node_modules/nprogress/nprogress.css'
import NProgress from 'nprogress'

export default {
    data() {
        return {
           selectedTheme: null,
           changing: false,
           passMsg: null,
           repeatNewPassword: null,
           newPassword: null
        }
    },

    computed: {
      lightText() {
        if(this.$store.state.currentTheme == 'blue' || this.$store.state.currentTheme == 'green' || this.$store.state.currentTheme == 'red' || this.$store.state.currentTheme == 'grey') {
          return true
        } else {
          return false
        }
      }
    },

    methods: {
        deleteAccount() {
            let conf = confirm('Are you sure you want to proceed?')
            if (conf) {
              NProgress.start()
              db.collection('users').doc(firebase.auth().currentUser.uid).delete().then(() => {
                firebase.auth().currentUser.delete().then(() => {
                    NProgress.done()
                    alert("We're sorry that you're leaving")
                    this.$router.go()
                }).catch(error => {
                    NProgress.done()
                    alert(error)
                });
              })
            }
        },

        changePass() {
            if(this.repeatNewPassword == this.newPassword && this.newPassword.length >= 6) {
                this.changing = true
                NProgress.start()
                firebase.auth().currentUser.updatePassword(this.newPassword).then(() => {
                    this.passMsg = 'Success, password has been changed'
                    this.newPassword = ''
                    this.repeatNewPassword = ''
                    NProgress.done()
                    this.changing = null
                }).catch(error => {
                    this.passMsg = error
                    this.newPassword = ''
                    this.repeatNewPassword = ''
                    NProgress.done()
                    this.changing = null
                });
            } else if(this.repeatNewPassword != this.newPassword ) {
                this.passMsg = 'Passwords do not match'
            } else if(this.repeatNewPassword == this.newPassword && this.newPassword.length < 6) {
                this.passMsg = 'Password must be at least 6 characters long'
            }
        },

        changeTheme() {
            NProgress.start()
            this.changing = true
            this.$store.state.currentTheme = this.selectedTheme
            db.collection('users').doc(firebase.auth().currentUser.uid).update({
                "currentTheme": this.selectedTheme
        }).then(() => {
            NProgress.done()
            this.changing = null
        })
        }
    }
}
</script>

<style lang="sass">
.v-input__append-inner
    display: none !important

.v-select__selections
    border: 1px solid #757575
    border-radius: 3px
    padding: 5px
    margin-top: 5px
    background-color: #f2f2f2 !important
    color: grey !important

.v-select__selections::after
    content: '▼'
    position: relative
    right: 5px

.passContainer > div > div:first-child
    background-color: #fff !important
    padding: 14px 
    margin-top: 10px
</style>
